.anim-container {
  overflow: hidden; }

.anim-left {
  transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  opacity: 0;
  transform: translateX(20px); }
  @media (min-width: 768px) {
    .anim-left {
      transform: translateX(80px); } }

.anim-right {
  transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  opacity: 0;
  transform: translateX(-20px); }
  @media (min-width: 768px) {
    .anim-right {
      transform: translateX(-80px); } }

.anim-up {
  transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  opacity: 0;
  transform: translateY(20px); }
  @media (min-width: 768px) {
    .anim-up {
      transform: translateY(80px); } }

.anim-complete {
  transform: translate(0, 0);
  opacity: 1; }

